.hrLogo {
	width: 80px;
	float: left;
}

.flexContainer {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.flexContainer2 {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.companyName {
	font-weight: 800;
	font-size: 3em;
	color: #154985;
	padding: 0;
	margin: 0;
}

.slogan {
	font-size: 1.4em;
	padding: 0;
	margin: 10px 0px 0px 0px;
	
}

.superFlexContainer{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

@media only screen and (max-width: 870px){
	.flexContainer2{
		scale: 0.9;
	}

	.triangles{
		scale: 0.9;
	}
}

@media only screen and (max-width: 415px){
	.flexContainer{
		-ms-flex-direction: column;
		    flex-direction: column;
		margin-top: 100px ;
	}

	.hrLogo{
		float: none;
		margin: auto;
		display: -ms-flexbox;
		display: flex;
	}

	.triangles{
		scale: 0.7;
		margin-top: -153px;
	}

	
	
}


