.superFlexDiv{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.subFlex{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    
    
}

.img{
    width: 520px;
    
}

.des{
    width: 400px;
    font-weight: 600;
}

.textContainer{
    margin: 0;
    
}

.title{
    color:#154985;
    font-size: 3em;
}

@media only screen and (max-width: 1080px){
    .superFlexDiv{
        scale: 0.9
    }
}

@media only screen and (max-width: 720px){
    .superFlexDiv{
        scale: 0.8
    }
}


