        flex-wrap: wrap;
}

.img{
    width: 50px;
    height: 50px;
    border-radius: 100px;
}

.whatWeDoContainer{
    color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
