    text-align: center;
    margin: 20px;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 135px;
}

.heading{
    margin: 0;
    padding: 0;
}

.des{
    margin: 0;
    padding: 0;
    margin-top: 10px; 
    margin-bottom: 40px;
}

@media only screen and (max-width: 500px){
    .thumbnailContainer{
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    }
    
}


