.flexLogo {
	overflow: auto;
	display: -ms-flexbox;
	display: flex;

	-ms-flex-direction: row;

	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.logo {
	width: 100px;
	height: 100px;
	margin: 0px;
	-webkit-transition: all 0.3s;
	-o-transition: all 0.3s;
	transition: all 0.3s;
	border-radius: 100px;
	border-style: none;
	margin-right: 26px;
	margin-top: 26PX;
	margin-bottom: 26px;
}

.logo:hover {
	-webkit-box-shadow: -31px 20px 42px -13px #a4a3a3;
	        box-shadow: -31px 20px 42px -13px #a4a3a3;
}

.title {
	font-weight: 800;
	font-size: 2em;
	color: white;
}

@media only screen and (max-width: 920px) {
	.flexLogo {
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
        margin: 0px;
		margin-left: 26px;
	}

	.title {
		text-align: center;
	}
}



